import React, { Component } from "react";
import { Container, Row, Col } from "reactstrap";
import HttpClient from "./../../utils/HttpClient";
//Import Breadcrumb
import Breadcrumbs from '../../components/Common/Breadcrumb';

//Import Components
import MiniWidgets from "./MiniWidgets";
import RevenueAnalytics from "./RevenueAnalytics";
import SalesAnalytics from "./SalesAnalytics";
import EarningReports from "./EarningReports";
import Sources from "./Sources";
import RecentlyActivity from "./RecentlyActivity";
import RevenueByLocations from "./RevenueByLocations";
import ChatBox from "./ChatBox";
import LatestTransactions from "./LatestTransactions";
import AcceptService from "./acceptservice";
import AcceptPrduct from "./acceptproduct";
import Totalwebsiteprofit from "./totalwebsiteprofit";
import SellerComission from "./sellercomission";
import TotalWebsiteRevenue from "./totalwebsiterevenue";

class Dashboard extends Component {
    constructor(props) {
        super(props);
        this.state = {
            breadcrumbItems: [
                { title: "Astrophy", link: "#" },
                { title: "Dashboard", link: "#" },
            ],
            reports: [
                {
                    icon: "ri-stack-line",
                    title: "Number of Sales",
                    value: "1452",
                    rate: "2.4%",
                    desc: "From previous period",
                },
                {
                    icon: "ri-store-2-line",
                    title: "Sales Revenue",
                    value: "$ 38452",
                    rate: "2.4%",
                    desc: "From previous period",
                },
                {
                    icon: "ri-briefcase-4-line",
                    title: "Average Price",
                    value: "$ 15.4",
                    rate: "2.4%",
                    desc: "From previous period",
                },
            ],
            activeuser: 0,
            approvedseller: 0,
            category: 0,
            service: 0,
            shop: 0,
            acceptserviceorder: 0,
            acceptservicerev: 0,
            acceptproductorder: 0,
            acceptproductrev: 0,
            total_rev: 0,
            website_profit: 0

        };
    }

    componentDidMount() {
        this.dasboardData();
        this.dasboardDataa();
        this.dasboardDataa1();
    };



    dasboardData = async () => {
        console.log("hiiu")
        let result = await HttpClient.requestData(
            "summary-stats",
            "GET",

        );

        console.log("Booking Accept", result);
        if (result && result.status) {
            this.setState({

                user: result.active_users,
                shop: result.active_shops,
                category: result.serv_category,
                service: result.active_shop_services,

                seller: result.approved_sellers,
            });


        } else {

        }
    }

    dasboardDataa = async () => {
        console.log("dash")
        let result = await HttpClient.requestData(
            "total-orders-and-revenues",
            "GET",

        );

        console.log("12", result);
        if (result && result.status) {
            let a = result.services.filter(it => it._id == "accept");
            let b = result.products.filter(it => it._id == "true");
            console.log(a);
            this.setState({
                result: result,
                total: result.total_bookings,
                pending: result.pending_bookings,
                accept: result.accepted_bookings,
                reject: result.rejected_bookings,
                user: result.user_count,
                seller: result.seller_count,
                acceptserviceorder: a[0].numberOfOrders,
                acceptservicerev: a[0].totalRevenue,
                acceptproductorder: b[0].numberOfOrders,
                acceptproductrev: b[0].totalRevenue
            });
            console.log(this.state.accept)



        } else {

        }
    }


    dasboardDataa1 = async () => {
        console.log("dash")
        let result = await HttpClient.requestData(
            "website-revenue-and-profit",
            "GET",

        );

        console.log("13ds", result);
        if (result && result.status) {
            // let a = result.services.filter(it => it._id == "accept");
            // let b = result.products.filter(it => it._id == "true");
            //console.log(a);
            this.setState({
                total_rev: result.total_revenue,
                website_profit: result.website_profit,
                seller_comission: result.total_seller_commission,
                website_rev: result.total_revenue,
            });
            // console.log(this.state.accept)



        } else {

        }
    }



    render() {
        return (
            <React.Fragment>
                <div className="page-content">
                    <Container fluid>

                        <Breadcrumbs title="Dashboard" breadcrumbItems={this.state.breadcrumbItems} />
                        <Row>
                            {/* <Col xl={8}>
                                <Row>
                                    <MiniWidgets reports={this.state.reports} />
                                </Row>
                                
                                <RevenueAnalytics/>
                            </Col> */}
                            {/* 
                            <Col xl={4}>

                                <SalesAnalytics name={"Sales"}/>

                                <EarningReports/>

                            </Col> */}
                        </Row>


                        <Row>
                            {/* sources */}
                            <Sources
                                totaluser={this.state.user ? this.state.user : 0}
                            />

                            {/* recent activity */}
                            <RecentlyActivity
                                totalseller={this.state.seller ? this.state.seller : 0}
                            />

                            {/* revenue by locations */}
                            <RevenueByLocations
                                totalshop={this.state.shop ? this.state.shop : 0}
                            />

                        </Row>


                        <Row>
                            {/* chat box */}
                            <SalesAnalytics
                                totalcategory={this.state.category ? this.state.category : 0}
                            />

                            {/* latest transactions */}
                            <LatestTransactions
                                totalservice={this.state.service ? this.state.service : 0}
                            />

                            <SellerComission
                                totalsellercomission={this.state.seller_comission ? this.state.seller_comission : 0}
                            />




                        </Row>

                        <Row>
                            {/* chat box */}
                            <Totalwebsiteprofit
                                profit={this.state.website_profit ? this.state.website_profit : 0}
                            />
                            <TotalWebsiteRevenue
                                rev={this.state.website_rev ? this.state.website_rev : 0}
                            />



                        </Row>
                        <Row>
                            <AcceptService
                                order={this.state.acceptserviceorder ? this.state.acceptserviceorder : 0}
                                rev={this.state.acceptservicerev ? this.state.acceptservicerev : 0}
                            />
                            {/* chat box */}
                            <AcceptPrduct
                                order={this.state.acceptproductorder ? this.state.acceptproductorder : 0}
                                rev={this.state.acceptproductrev ? this.state.acceptproductrev : 0}
                            />

                            {/* latest transactions */}

                        </Row>

                    </Container>
                </div>
            </React.Fragment>
        );
    }
}

export default Dashboard;
