import React, { Component } from "react";
import axios from "axios";

import {
    Card,
    CardBody,
    Col,
    Row,
    Container,
    FormGroup,
    Label,
    Input,
    CustomInput,
    Button,
    UncontrolledAlert,
    InputGroup,
    InputGroupAddon,
} from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";
import { MDBDataTable } from "mdbreact";
import { reactLocalStorage } from "reactjs-localstorage";
import moment from "moment";
import Dropzone from "react-dropzone";
import BaseInfo from "../ChooseDesign/baseInfo";
import HttpClient from "./../../utils/HttpClient";
// import EcomPreview from "./E-ComPreview/index";
import Select from "react-select";
import ColorPicker from "@vtaits/react-color-picker";
import "@vtaits/react-color-picker/dist/index.css";
import index from "../ChooseDesign";
// import ElementMirror from "@fullcalendar/interaction/dnd/ElementMirror";
import { CSVLink } from "react-csv";

const header = [


    { label: "Name", key: "username" },
    { label: "Email", key: "email" },
    { label: "About", key: "about" },
    { label: "Address", key: "address" },
    { label: "Description", key: "desc" },
    { label: "Date", key: "date" },



]


class FormElements extends Component {
    constructor(props) {
        super(props);
        this.state = {
            breadcrumbItems: [
                { title: "All Report", link: "#" },
                { title: "Show All Report", link: "#" },
            ],
            customchk: true,
            toggleSwitch: true,
            // page states
            name: "",
            email: "",
            mobile: "",
            password: "",
            data: [],
            alert: false,
            message: "",
            type: "",
            result: [],

        };

    }

    componentDidMount = async () => {
        // this.checkUser();
        // this.fetchService();
        this.fetchData();

    };




    fetchData = async () => {

        let result = await HttpClient.requestData("problem-report", "GET");
        console.log("seller-requests", result);
        if (result && result.status) {
            let data = [];
            let i = 1;
            this.setState({ result: result.data });
            // console.log(this.state.pointarr);
            this.state.result.forEach((element, index) => {
                console.log("ee", element.user_data[0]);
                let rows = {
                    sl: i,
                    date: moment(element.date).format("DD/MM/YYYY"),
                    username: element.user_data[0].firstName + "" + element.user_data[0].lastName,
                    email: element.user_data[0].email,
                    about: element.user_data[0].about,
                    city: element.user_data[0].city,
                    desc: element.desc
                    // phone: element.phone,
                    // govt_name: element.govt_id_name,
                    // govt_id: element.govt_id,


                };
                i++;
                data.push(rows);
                console.log("data", data);
            });
            this.setState({
                data: data,
            });
        } else {
            this.setState({
                data: [],
            });
        }


    };





    render() {
        const data = {
            columns: [
                {
                    label: "Sl.",
                    field: "sl",
                    sort: "asc",
                    width: 150,
                },

                {
                    label: "Name",
                    field: "username",
                    sort: "asc",
                    width: 270,
                },
                {
                    label: "Email",
                    field: "email",
                    sort: "asc",
                    width: 270,
                },
                {
                    label: "About",
                    field: "about",
                    sort: "asc",
                    width: 270,
                },

                {
                    label: "Address",
                    field: "city",
                    sort: "asc",
                    width: 270,
                },
                {
                    label: "Desc",
                    field: "desc",
                    sort: "asc",
                    width: 270,
                },
                {
                    label: "Date",
                    field: "date",
                    sort: "asc",
                    width: 270,
                },




                // {
                //     label: "Feedback Detail",
                //     field: "feedback_detail",
                //     sort: "asc",
                //     width: 270,
                // },


            ],
            rows: this.state.data,
        };
        return (
            <React.Fragment>
                <div className="page-content">
                    <Container fluid>
                        <Breadcrumbs
                            title={this.state.breadcrumbItems[0].title}
                            breadcrumbItems={this.state.breadcrumbItems}
                        />

                        <Row>
                            <Col xl={12}>
                                {this.state.data.length > 0 ?
                                    <CSVLink
                                        data={this.state.data}
                                        headers={header}
                                        filename={"allreport.csv"}
                                        className="btn btn-success">
                                        Export Report in CSV
                                    </CSVLink> : null}
                                <div className="card p-3">



                                    <MDBDataTable responsive bordered data={data} />
                                </div>
                            </Col>
                        </Row>
                    </Container>
                </div>
            </React.Fragment>
        );
    }
}

export default FormElements;
