import React, { Component } from "react";
import {
    Card,
    CardBody,
    Col,
    Row,
    Container,
    FormGroup,
    Label,
    Input,
    CustomInput,
    Button,
    UncontrolledAlert,
} from "reactstrap";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import { confirmAlert } from "react-confirm-alert"; // Import
import "react-confirm-alert/src/react-confirm-alert.css"; // Import css
//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";
import { MDBDataTable, MDBDataTableV5 } from "mdbreact";
import { reactLocalStorage } from "reactjs-localstorage";
import moment from "moment";
import { CSVLink } from "react-csv";
import HttpClient from "./../../utils/HttpClient";
class FormElements extends Component {
    constructor(props) {
        super(props);
        this.state = {
            breadcrumbItems: [
                { title: "Currency", link: "#" },
                { title: "Add & Manage Currency", link: "#" },
            ],
            customchk: true,
            toggleSwitch: true,
            // page states
            currency: "",
            tax: 0,
            data: [],
            alert: false,
            message: "",
            type: "",
            result: [],
            edit_enable: false,
            edit_item_id: "",
            image_select: false,
            img_url: "",
            category_type: "",
            description: "",
            size: [{ name: "" }],
            currencyTyp: [
                {
                    name: "INR"
                },
                {
                    name: "USD"
                },
                {
                    name: "GBP"
                },
                {
                    name: "EUR"
                },
                {
                    name: "CAD"
                },
                {
                    name: "cdd"
                }

            ]
        };
    }

    componentDidMount = async () => {
        this.fetchData();
    };

    fetchData = async () => {
        let result = await HttpClient.requestData("currency", "GET");
        console.log("currency", result);
        if (result && result.status > 0) {
            let data = [];
            let i = 1;
            this.setState({ result: result.data });
            this.state.result.forEach((element, index) => {
                let rows = {
                    sl: i,
                    cur_type: element.abbreviation,
                    date: moment(element.created_on).format("MMMM Do YYYY"),
                    tax: element.tax_rate,
                    action: (
                        <>
                            <button
                                title="Delete"
                                className="btn btn-danger mr-2"
                                onClick={() => {
                                    this.delete(element, index);
                                }}
                            >
                                <i className="fa fa-trash" />
                            </button>
                            {/* <button
                                title="Edit"
                                className="btn btn-primary"
                                onClick={() => {
                                    this.edit(element, index);
                                }}
                            >
                                <i className="fa fa-edit" />
                            </button> */}
                        </>
                    ),

                };
                i++;
                data.push(rows);
            });
            this.setState({
                data: data,
            });
        } else {
            this.setState({
                data: [],
            });
        }
    };
    delete = async (item, index) => {
        confirmAlert({
            title: "Confirm to delete",
            message: "Are you sure want to do this?",
            buttons: [
                {
                    label: "Yes",
                    onClick: () => this.deleteapi(item),
                },
                {
                    label: "No",
                    // onClick: () => alert("Click No"),
                },
            ],
        });
    };

    deleteapi = async (item) => {

        let result = await HttpClient.requestData(
            "currency/" + item._id,
            "DELETE",

        );
        if (result && result.status) {
            this.fetchData();
        }
    };
    edit = async (item, index) => {
        this.setState({
            edit_enable: true,
            name: item.name,
            description: item.content,
            edit_item_id: item._id,
        });
    };
    submit = async () => {
        if (this.state.currency != "" && this.state.tax != "") {
            let data = null;
            let result = null;

            if (this.state.edit_enable == false) {
                data = {


                    abbreviation: this.state.currency,

                    tax_rate: this.state.tax,
                    name: "name",

                };
                console.log("data", data)
                result = await HttpClient.requestData(
                    "currency",
                    "POST",
                    data
                );
            } else {
                data = {
                    abbreviation: this.state.currency,

                    tax_rate: this.state.tax,
                    name: "name",
                };
                console.log(data);
                result = await HttpClient.requestData(
                    "currency" + this.state.edit_item_id,
                    "PUT",
                    data
                );
            }
            console.log("result", result);
            if (result && result.status) {
                this.setState({
                    alert: true,
                    message: this.state.edit_enable
                        ? "Currency Updated Successfully"
                        : "Currency Added Successfully",
                    type: "success",
                    currency: "",
                    tax: 0,

                    edit_enable: false,
                    edit_item_id: "",
                });

                this.fetchData();
            } else {
                this.setState({
                    alert: true,
                    message: result.error,
                    type: "danger",
                });
            }
        } else {
            this.setState({
                alert: true,
                message: "Please Fill Up All Details.",
                type: "warning",
            });
        }
        setTimeout(() => {
            this.setState({
                alert: false,
                message: "",
                type: "",
            });
        }, 3000);
    };

    handleChange = (event) => {
        console.log(event.target.value);
        this.setState({ category_type: event.target.value });
    };

    imageUpload = async (e) => {
        console.log("e", e.target.files);
        let file = e.target.files;
        //   if(file.lenght>0)
        //   {
        let dataSend = { buckate: true, buckate_name: "appIcon" };

        let result = await HttpClient.newFileUpload(
            "image-upload/category",
            e.target.files[0],
            dataSend
        );
        // console.log("result", result);
        if (result && result.status) {
            this.setState({ img_url: result.url, image_select: true });
        }

        //   }
    };

    status = async (index, id) => {
        let sendData = {};
        let result = await HttpClient.requestData(
            "category/set-status/" + id,
            "PUT",
            sendData
        );
        if (result && result.status) {
            this.fetchData();
        }
    };

    render() {
        const data = {
            columns: [
                {
                    label: "Sl.",
                    field: "sl",

                    width: 150,
                },
                {
                    label: "Currency Type",
                    field: "cur_type",

                    width: 150,
                },
                {
                    label: "Tax",
                    field: "tax",

                    width: 150,
                },



                {
                    label: "Action",
                    field: "action",

                    width: 100,
                },

            ],
            rows: this.state.data,
        };

        const header = [

            { label: "Sl.", key: "sl" },
            { label: "Currency", key: "cur_type" },
            { label: "Tax", key: "tax" },

        ]

        return (
            <React.Fragment>
                <div className="page-content">
                    <Container fluid>
                        <Breadcrumbs
                            title={this.state.breadcrumbItems[0].title}
                            breadcrumbItems={this.state.breadcrumbItems}
                        />

                        <Row>
                            <Col xs={12}>
                                <Card>
                                    <CardBody>
                                        {this.state.alert ? (
                                            <UncontrolledAlert
                                                color={this.state.type}
                                                className="alert-dismissible fade show"
                                                role="alert"
                                            >
                                                {this.state.type == "warning" ? (
                                                    <i className="mdi mdi-alert-outline mr-2"></i>
                                                ) : this.state.type == "success" ? (
                                                    <i className="mdi mdi-check-all mr-2"></i>
                                                ) : this.state.type == "danger" ? (
                                                    <i className="mdi mdi-block-helper mr-2"></i>
                                                ) : null}
                                                {this.state.message}
                                            </UncontrolledAlert>
                                        ) : null}
                                        <FormGroup row>
                                            <Label
                                                htmlFor="example-text-input"
                                                className="col-md-2 col-form-label"
                                            >
                                                Currency
                                            </Label>
                                            <Col md={10}>

                                                <select
                                                    className="form-control"
                                                    onChange={(val) => {
                                                        this.state.currency = val.target.value;
                                                        this.setState({

                                                        })
                                                    }}
                                                    value={this.state.cureency}
                                                >
                                                    <option value="">Select Currency</option>
                                                    {this.state.currencyTyp.map((item, index) => {
                                                        return (<option value={item.name}>{item.name}</option>)
                                                    })}

                                                </select>
                                            </Col>
                                        </FormGroup>

                                        <FormGroup row>
                                            <Label
                                                htmlFor="example-text-input"
                                                className="col-md-2 col-form-label"
                                            >
                                                Tax(%)
                                            </Label>
                                            <Col md={10}>
                                                <Input
                                                    className="form-control"
                                                    type="text"
                                                    value={this.state.tax}
                                                    placeholder="Enter Category"
                                                    id="example-text-input"
                                                    onChange={(val) => {
                                                        this.setState({ tax: val.target.value });
                                                    }}
                                                />
                                            </Col>
                                        </FormGroup>

                                        <FormGroup className="mb-0">
                                            <div className="button-items pt-4">
                                                <Button
                                                    color="primary"
                                                    type="button"
                                                    className="waves-effect waves-light mr-1"
                                                    onClick={() => {
                                                        this.submit();
                                                    }}
                                                >
                                                    {this.state.edit_enable ? "Update" : "Submit"}{" "}
                                                    <i className="ri-arrow-right-line align-middle ml-1"></i>
                                                </Button>
                                            </div>
                                        </FormGroup>
                                    </CardBody>
                                </Card>
                            </Col>
                        </Row>
                        <Row>
                            <Col xl={3}>
                                {this.state.data.length > 0 ?
                                    <CSVLink
                                        data={this.state.data}
                                        headers={header}
                                        filename={"Category.csv"}
                                        className="btn btn-success">
                                        Export Report in CSV
                                    </CSVLink> : null}
                            </Col>
                        </Row>&nbsp;
                        <Row>
                            <Col xl={12}>
                                <div className="card p-3">
                                    {/* <MDBDataTable responsive bordered data={data} /> */}
                                    <MDBDataTableV5
                                        hover
                                        entriesOptions={[5, 20, 25]}
                                        entries={5}
                                        pagesAmount={4}
                                        data={data}
                                    />
                                </div>
                            </Col>
                        </Row>
                    </Container>
                </div>
            </React.Fragment>
        );
    }
}

export default FormElements;
